@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
	html {
		font-family: 'DM Sans', sans-serif !important;
		font-feature-settings: 'kern' !important;
		-webkit-font-smoothing: antialiased;
		letter-spacing: -0.5px;
	}
}

input.defaultCheckbox::before {
	content: url(../src/assets/svg/checked.svg); 
	color: white;
	opacity: 0; 
	height: 16px; 
	width: 16px;
	position: absolute; 
	left: 50%;
	transform: translate(-50%, 0px);
} 

input:checked.defaultCheckbox::before { 
	opacity: 1;
}

body.font-dm.bg-lightPrimary.dark {
    --tw-bg-opacity: 1 !important;
    background-color: rgb(11 20 55 / 1 ) !important;
}

@media  (max-width:767px) {
	.filterbar {
		display: inline-block;
		width: 100%;
	}
	
	.all {
		width: 50%;
		float: left;
		background: #fff;
		font-size: 14px;
		padding: 5px;
		margin-top: 15px;
		text-align: left;
		border-radius: 15px 0 0 15px ;
	}
	
	.per {
		float: right;
		background: #fff;
		border-radius: 0 15px  15px 0;
	}

	.stock-panel {
		overflow: hidden;
		padding: 5px 10px;
	}

	.stock-panel ul {
		display: flex;
		padding: 0 10px;
		overflow: auto;
	}

	.stock-panel li {
		padding: 2px 7px;
		font-size: 14px;
	}
	.m-hidden{
		display: none;
	}
	.mob-top1 {
		width: 39%;
	}
	.mob-profile {
     margin-top: 15px;
	}
	
	.mob-top2 {
		width: 20%;
	}
	.mob-top2 img{
		margin: auto;
	}
	.mob-top3 {
		width: 39%;
		display: inline-block;
	}
	.mob-top2 img {
		margin: 0 auto;
		width: 35px;
	}
	.top1-inner .relative.flex:nth-child(2) {
		display: block;
		top: 0px;
		margin-top: 15px;
		font-size: 14px;
	}
	.mob-top1 button.flex.items-center.justify-center {
		margin-top: 15px;
		width: 100%;
		max-width: 100%;
		font-size: 12px;
	}
	.mob-top1 .top1-inner.flex.items-center {
		display: inline;
	}
	.backtodashboard {
		font-size: 11px;
		border-radius: 20px;
		height: 30px;
		margin-top: 18px;
		position: relative;
		top: -2px;
	}
	.backtodashboard svg {
		width: 20px;
	}
	ul.month-calendar {
		width: 100%;
		overflow: auto;
		display: block;
	}
	.month-calendar li {
		padding: 5px 10px;
		display: inline;
		white-space: nowrap;
		margin-right: 7px;
	}
	.total-trade-bar {
		border-radius: 20px;
		padding: 15px 15px 0px;
	}
	.total-trade-bar li {
		width: 33.3333%;
		margin-bottom: 10px;
	}
	.right-mob-arrow{
       top: 42px;
	   right: -15px;
	   width: auto !important;
	}
	.left-mob-arrow{
        top:42px;
		left: -15px;
		width: auto !important;

	}
	.coupon {
		text-align: center;
		display: grid;
	}
	.coupon p {
		order: 2;
		margin-bottom: 10px;
		margin-top: 10px;
		right:0;
	}
	.smart {
		width: 100%;
		border-radius: 50px;
		padding: 10px 25px;
		text-align: center;
	}

	ul.month-calendar.year-calender {
		height: 35px;
	}
	.mob-search p {
		top: 14px;
	}
}